import React from 'react';
import { useNavigate } from 'react-router-dom';
import Form from "../../../containers/form/form";
import Field from "../../../containers/form/field";
import Button from "../../../components/ui/button";
import {usePostQuery} from "../../../hooks/api";
import {useSettingsStore} from "../../../store";
import {get} from "lodash";
import Swal from "sweetalert2";
import {OverlayLoader} from "../../../components/loader";
import {URLS} from "../../../constants/url";

const LoginContainer = ({...rest}) => {
    const {mutate,isLoading} = usePostQuery({url:URLS.login,hideSuccessToast:true})
    const setToken = useSettingsStore(state => get(state, 'setToken', () => {
    }))
    const navigate = useNavigate();

    const loginRequest = ({data}) => {
        mutate({url: URLS.login, attributes: data}, {
            onSuccess: ({data}) => {
                setToken(get(data, 'token', null))
                navigate("/dashboard");
                Swal.fire({
                    position: 'center',
                    icon: 'success',
                    backdrop: 'rgba(0,0,0,0.9)',
                    background: 'none',
                    title: '    ',
                    showConfirmButton: false,
                    timer: 2000,
                    customClass: {
                        title: 'title-color',
                    },
                });
            }
        })
    }

    if(isLoading){
        return <OverlayLoader />
    }

    return (
        <div>
            <Form formRequest={loginRequest} footer={<Button className={'form-btn'} type={'submit'}>Login</Button>}>
                <Field name={'email'} type={'input'} label={'Email'} property={{type: 'email'}} params={{
                    required: true,
                    pattern: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                }}/>
                <Field name={'password'} type={'input'} label={'Password'} property={{type: 'password'}}
                       params={{required: true}}/>
            </Form>
        </div>
    );
};

export default LoginContainer;